<template>
    <div class="container">
        <div class="logo" style="position: absolute;left: 106px;top:90px;height: 50px;line-height: 50px;">
            <img src="../../assets/images/bg-logo.png" alt="" style="float:left">
            <span style="display: inline-block;font-size: 20px;color:#fff;float:left"><i style="margin: 0 30px;">|</i>收支盈亏一目了然</span>
        </div>
        <div class="text" style="position: absolute;top: 240px;left: 196px;">
            <p style="color: #FFF;font-size: 20px;">账号登录</p>
        </div>
        <el-form :model="form" ref="form" :rules="rules2" label-position="left" label-width="0px"
                 class="demo-ruleForm card-box loginform">
            <div class="longinDiv">
                <!--<div class="login-title">-->
                    <!--<img src="../../assets/images/title.png">-->
                    <!--<p class="titlename">{{systemName}}</p>-->
                <!--</div>-->
                <el-form-item prop="username" class="setconfig">
                    <el-input type="text" v-model="form.username" auto-complete="off" placeholder="账号">
                        <i slot="prefix" class="icon iconfont iconset icon-wode"></i>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password" class="setconfig1">
                    <el-input type="password" v-model="form.password" auto-complete="off" placeholder="密码">
                        <i slot="prefix" class="icon iconfont iconset icon-mimasuo"></i>
                    </el-input>
                </el-form-item>
                <el-form-item prop="verifyCode" class="setconfig1" style="display:none">
                    <el-input type="text" v-model="form.verifyCode" auto-complete="off" placeholder="验证码"
                              style='width: 180px'></el-input>
                    <!-- <el-button class="btncode" type="primary" @click="codeTap" :disabled="disabled">{{find}}</el-button> -->
                </el-form-item>
                <el-form-item class="Btnform">
                    <el-button class="longinbtn" :loading="loading" @click.native.prevent="handleSubmit2('form')">登录
                    </el-button>
                </el-form-item>
            </div>
        </el-form>
    </div>
</template>
<script>
  import $ from 'jquery'
  import http from '../../assets/js/http'

  export default {
    data () {
      return {
        title: '',
        systemName: '',
        loading: false,
        form: {
          username: '',
          password: '',
          verifyCode: '',
        },
        requireVerify: false,
        verifyUrl: '',
        verifyImg: window.HOST + '/admin/base/getVerify',
        rules2: {
          username: [
            {required: true, message: '请输入账号', trigger: 'blur'},
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
          ],
          verifyCode: [
            {required: false, message: '请输入验证码', trigger: 'blur'},
          ],
        },
        checked: false,
      }
    },
    methods: {
      refreshVerify () {
        this.verifyUrl = ''
        setTimeout(() => {
          this.verifyUrl = this.verifyImg + '?v=' + moment().unix()
        }, 300)
      },
      handleSubmit2 (form) {
        if (this.loading) return
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.loading = !this.loading
            let data = {}
            if (this.requireVerify) {
              data.username = this.form.username
              data.password = this.form.password
              data.verifyCode = this.form.verifyCode
            } else {
              data.username = this.form.username
              data.password = this.form.password
            }
            if (this.checked) {
              data.isRemember = 1
            } else {
              data.isRemember = 0
            }
            this.apiPost('admin/base/login', data).then((res) => {
              if (res.code != 200) {
                this.loading = !this.loading
                this.handleError(res)
              } else {
                this.refreshVerify()
                if (this.checked) {
                  Cookies.set('rememberPwd', true, {expires: 1})
                }
                this.resetCommonData(res.data)
                _g.toastMsg('success', '登录成功')
              }
            })
          } else {
            return false
          }
        })
      },
      checkIsRememberPwd () {
        if (Cookies.get('rememberPwd')) {
          let data = {
            rememberKey: Lockr.get('rememberKey'),
          }
          this.apiPost('admin/base/relogin', data).then((res) => {
            this.handelResponse(res, (data) => {
              this.resetCommonData(data)
            })
          })
        }
      },
    },
    created () {
      this.checkIsRememberPwd()
      this.apiPost('admin/base/getConfigs').then((res) => {
        this.handelResponse(res, (data) => {
          Lockr.set('contract', data.CONTRACT)
          document.title = data.SYSTEM_NAME
          this.systemName = data.SYSTEM_NAME
          if (parseInt(data.IDENTIFYING_CODE)) {
            this.requireVerify = true
            this.rules2.verifyCode[0].required = true
          }
        })
      })
      this.verifyUrl = this.verifyImg
    },
    mounted () {
      $('input').addClass('setinput')
      window.addEventListener('keyup', (e) => {
        if (e.keyCode === 13) {
          this.handleSubmit2('form')
        }
      })
    },
    mixins: [http],
  }
</script>
<style scoped>
    .container {
        width: 100%;
        height: 100vh;
        background: url("../../assets/images/background.png");
        background-size: 100% 100%;
    }

    .card-box {
        border: none !important;
        background: transparent;
        margin-top: 0 !important;
    }

    .loginform {
        position: absolute;
        z-index: 1000;
        width: 100%;
        top: 20%;
        left: 0;
    }

    .longinDiv {
        position: absolute;
        left: 265px;
        top: 180px;
        background: #fff;
        width: 300px;
        padding-top: 40px;
        border-radius: 5px;
        box-shadow: 1px 2px 2px #4777C9;
    }

    .login-title {
        width: 100%;
        height: 150px;
        text-align: center;
    }

    .login-title img {
        height: 25%;
        margin-top: 35px;
    }

    .titlename {
        color: #666666;
        font-size: 23px;
        font-weight: bold;
        padding: 0;
        margin: 0;
        margin-top: 10px;
    }

    .setconfig,
    .setconfig1 {
        height: 50px;
        border: none;
        width: 80%;
        margin-bottom: 1px;
        margin: 15px auto;
    }

    .Btnform {
        width: 100%;
    }

    .longinbtn {
        display: block;
        width: 80%;
        margin: 20px auto;
        height: 45px;
        color: white;
        font-size: 18px;
        background: #3562EC;
        border: none;
        border-radius: 1px;
        box-shadow: 1px 2px 2px #4777C9;
    }

    /*input:-webkit-autofill {
     -webkit-box-shadow: 0 0 0px 1000px white inset;
     -webkit-text-fill-color: #333;
    }*/
    /*input:-webkit-autofill,
        input:-webkit-autofill:hover,
        input:-webkit-autofill:focus,
        input:-webkit-autofill:active {
            -webkit-transition-delay: 99999s;
            -webkit-transition: color 99999s ease-out, background-color 99999s ease-out;
        }*/
</style>



